import React, { useState, useEffect } from 'react';
import { 
  User, ShoppingCart, Clock, FileText, AlertTriangle, 
  ChevronDown, ChevronUp, Check, X, Award, RefreshCw 
} from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import {
  app,
  db,
  getAuth,
  doc,
  getDoc,
  updateDoc,
  getFirestore,
  collection,
  query,
  where,
  getDocs,
  getImageURL,
  serverTimestamp,
  writeBatch,
  orderBy
} from '../firebase/firebaseConfig';


import { Home, Calendar } from 'lucide-react';



const AdminPage = () => {
  const navigate = useNavigate();
  const [admin, setAdmin] = useState(null);
  const [pendingRentals, setPendingRentals] = useState([]);
  const [activeRentals, setActiveRentals] = useState([]);
  const [returnRequests, setReturnRequests] = useState([]);
  const [loading, setLoading] = useState(true);
  const [imageUrls, setImageUrls] = useState({});
  const [activeTab, setActiveTab] = useState('pending');
  const [expandedItems, setExpandedItems] = useState({});
  const [selectedItems, setSelectedItems] = useState([]);
  const [penaltyModalOpen, setPenaltyModalOpen] = useState(false);
  const [currentUser, setCurrentUser] = useState(null);
  const [penaltyPoints, setPenaltyPoints] = useState(0);
  const [penaltyReason, setPenaltyReason] = useState('');
  const [sortBy, setSortBy] = useState('userName'); // Default sort by user name

  //     
  const approveSelectedRentals = async () => {
    if (selectedItems.length === 0) {
      alert('  .');
      return;
    }

    if (!confirm(` ${selectedItems.length}  ?`)) return;

    try {
      const batch = writeBatch(db);

      for (const rentalId of selectedItems) {
        const rentalRef = doc(db, 'reservations', rentalId);
        batch.update(rentalRef, {
          status: 'active',
          approvedAt: serverTimestamp(),
          approvedBy: admin.uid,
        });
      }

      await batch.commit();

      // Update equipment status for each approved rental
      for (const rentalId of selectedItems) {
        const rentalRef = doc(db, 'reservations', rentalId);
        const rentalDoc = await getDoc(rentalRef);
        const rentalData = rentalDoc.data();
        
        if (rentalData.equipmentId) {
          const equipmentRef = doc(db, 'cameras', rentalData.equipmentId);
          await updateDoc(equipmentRef, {
            status: 'rented',
            lastRentalId: rentalId
          });
        }
      }

      alert(`${selectedItems.length}  .`);
      setSelectedItems([]);
      fetchRentalData();
    } catch (error) {
      console.error('Error approving rentals:', error);
      alert('    .');
    }
  };

  //     
  const rejectSelectedRentals = async () => {
    if (selectedItems.length === 0) {
      alert('  .');
      return;
    }

    if (!confirm(` ${selectedItems.length}  ?`)) return;

    try {
      const batch = writeBatch(db);

      for (const rentalId of selectedItems) {
        const rentalRef = doc(db, 'reservations', rentalId);
        batch.update(rentalRef, {
          status: 'rejected',
          rejectedAt: serverTimestamp(),
          rejectedBy: admin.uid,
        });
      }

      await batch.commit();

      alert(`${selectedItems.length}  .`);
      setSelectedItems([]);
      fetchRentalData();
    } catch (error) {
      console.error('Error rejecting rentals:', error);
      alert('    .');
    }
  };

  //  
  const handleHomeNavigation = () => {
    navigate('/main');
  };

  const handleCalendarNavigation = () => {
    navigate('/calendar');
  };

  const handleNoteNavigation = () => {
    navigate('/thingsnote');
  };

  const handleReservateNavigation = () => {
    navigate('/ReservationMainPage');
  };

  // Page load - authentication and data fetching
  useEffect(() => {
    const auth = getAuth();
    const unsubscribe = auth.onAuthStateChanged((firebaseUser) => {
      if (firebaseUser) {
        //   
        checkAdminRole(firebaseUser.uid).then(isAdmin => {
          if (isAdmin) {
            const adminData = {
              uid: firebaseUser.uid,
              email: firebaseUser.email,
              name: firebaseUser.displayName || ''
            };
            
            setAdmin(adminData);
            fetchRentalData();
          } else {
            //      
            alert('  .');
            navigate('/main');
          }
        });
      } else {
        console.log('Firebase   ');
        setAdmin(null);
        setLoading(false);
        navigate('/login');
      }
    });

    return () => unsubscribe();
  }, []);

  //   
  const checkAdminRole = async (userId) => {
    try {
      const userRef = doc(db, 'user_profiles', userId);
      const userDoc = await getDoc(userRef);
      
      if (userDoc.exists()) {
        return userDoc.data().role === 'admin';
      }
      return false;
    } catch (error) {
      console.error('Error checking admin role:', error);
      return false;
    }
  };

  //   
  const fetchRentalData = async () => {
    try {
      const pendingQuery = query(
        collection(db, 'reservations'),
        where('status', '==', 'pending')
      );
      const pendingSnapshot = await getDocs(pendingQuery);
      const pendingData = pendingSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      
      //     ( )
      const enhancedPendingData = await Promise.all(pendingData.map(async (rental) => {
        if (rental.userId && !rental.userName) {
          try {
            const userRef = doc(db, 'user_profiles', rental.userId);
            const userDoc = await getDoc(userRef);
            if (userDoc.exists()) {
              const userData = userDoc.data();
              return {
                ...rental,
                userName: userData.name || userData.displayName || ' ',
                userEmail: userData.email || rental.userEmail || ' '
              };
            }
          } catch (error) {
            console.error('Error fetching user data:', error);
          }
        }
        return rental;
      }));
      
      //  
      const sortedPendingData = sortRentalData(enhancedPendingData, sortBy);
      setPendingRentals(sortedPendingData);
      
      // 2.     
      const activeQuery = query(
        collection(db, 'reservations'),
        where('status', '==', 'active')
      );
      
      const activeSnapshot = await getDocs(activeQuery);
      const activeData = activeSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setActiveRentals(activeData);
      
      // 3.   
      const returnQuery = query(
        collection(db, 'reservations'),
        where('status', '==', 'return_requested')
      );
      
      const returnSnapshot = await getDocs(returnQuery);
      const returnData = returnSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setReturnRequests(returnData);
      
      //    
      const allItems = [...enhancedPendingData, ...activeData, ...returnData];
      if (allItems.length > 0) {
        fetchItemImages(allItems);
      }
      
      setLoading(false);
    } catch (error) {
      console.error('Error fetching rental data:', error);
      setLoading(false);
    }
  };

  //   
  const sortRentalData = (data, sortKey) => {
    return [...data].sort((a, b) => {
      switch (sortKey) {
        case 'userName':
          return (a.userName || '').localeCompare(b.userName || '');
        case 'rentalDate':
          return new Date(a.rentalDate) - new Date(b.rentalDate);
        case 'name': // Equipment name
          return (a.name || '').localeCompare(b.name || '');
        case 'userId':
          return (a.userId || '').localeCompare(b.userId || '');
        default:
          return 0;
      }
    });
  };

  //   
  const handleSortChange = (key) => {
    setSortBy(key);
    
    //      
    if (activeTab === 'pending') {
      setPendingRentals(sortRentalData(pendingRentals, key));
    } else if (activeTab === 'active') {
      setActiveRentals(sortRentalData(activeRentals, key));
    } else if (activeTab === 'return') {
      setReturnRequests(sortRentalData(returnRequests, key));
    }
  };

  //    
  const fetchItemImages = async (items) => {
    const urls = {};
    for (const item of items) {
      if (item.image) {
        try {
          const url = await getImageURL(item.image);
          urls[item.id] = url;
        } catch (error) {
          console.error(`Error loading image for ${item.name}:`, error);
          urls[item.id] = null;
        }
      }
    }
    setImageUrls(urls);
  };

  //   
  const toggleSelectItem = (itemId) => {
    setSelectedItems(prev => {
      if (prev.includes(itemId)) {
        return prev.filter(id => id !== itemId);
      } else {
        return [...prev, itemId];
      }
    });
  };

  //   /
  const toggleSelectAll = (items) => {
    if (selectedItems.length === items.length) {
      setSelectedItems([]);
    } else {
      setSelectedItems(items.map(item => item.id));
    }
  };

  //   
const approveRental = async (rentalId) => {
  try {
    // 1. Firebase  
    const rentalRef = doc(db, 'reservations', rentalId);
    await updateDoc(rentalRef, {
      status: 'active',
      approvedAt: serverTimestamp(),
      approvedBy: admin.uid
    });

    // 2.   'rented' 
    const rentalDoc = await getDoc(rentalRef);
    const rentalData = rentalDoc.data();

    if (rentalData.equipmentId) {
      const equipmentRef = doc(db, 'cameras', rentalData.equipmentId);
      await updateDoc(equipmentRef, {
        status: 'rented',
        lastRentalId: rentalId
      });
    }

    // 3.  Google Calendar  
    const response = await fetch('/api/addEventToCalendar', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        title: `[  ] ${rentalData.name}`,
        description: `: ${rentalData.userName || rentalData.userId}\n : ${rentalData.purpose || ''}`,
        startDate: rentalData.rentalDate,
        startTime: rentalData.rentalTime,
        endDate: rentalData.returnDate,
        endTime: rentalData.returnTime
      })
    });

    if (!response.ok) {
      throw new Error('  ');
    }

    alert('  ,   .');
    fetchRentalData();
  } catch (error) {
    console.error('Error approving rental:', error);
    alert('    .');
  }
};



  //    (Missing function implementation)
  const rejectRental = async (rentalId) => {
    try {
      const rentalRef = doc(db, 'reservations', rentalId);
      await updateDoc(rentalRef, {
        status: 'rejected',
        rejectedAt: serverTimestamp(),
        rejectedBy: admin.uid
      });

      alert('  .');
      fetchRentalData();
    } catch (error) {
      console.error('Error rejecting rental:', error);
      alert('   .');
    }
  };

  //   (Missing function implementation)
  const processReturn = async (rentalId) => {
    try {
      // Get rental data first
      const rentalRef = doc(db, 'reservations', rentalId);
      const rentalDoc = await getDoc(rentalRef);
      const rentalData = rentalDoc.data();
      
      // Ask if there are any damages
      const hasIssues = confirm('   ? (: , : )');
      
      if (hasIssues) {
        // Open penalty modal if there are issues
        setCurrentUser({
          id: rentalData.userId,
          name: rentalData.userName || '',
          rentalId: rentalId
        });
        setPenaltyModalOpen(true);
      } else {
        // Normal return process
        await updateDoc(rentalRef, {
          status: 'returned',
          returnedAt: serverTimestamp(),
          processedBy: admin.uid,
          returnStatus: 'normal'
        });
        
        // Update equipment status
        if (rentalData.equipmentId) {
          const equipmentRef = doc(db, 'cameras', rentalData.equipmentId);
          await updateDoc(equipmentRef, {
            status: 'available',
            lastRentalId: null
          });
        }
        
        alert('  .');
        fetchRentalData();
      }
    } catch (error) {
      console.error('Error processing return:', error);
      alert('   .');
    }
  };

  //     
  const applyPenaltyAndProcessReturn = async () => {
    if (!currentUser || penaltyPoints <= 0 || !penaltyReason) {
      alert('   .');
      return;
    }
    
    try {
      // 1.   
      const rentalRef = doc(db, 'reservations', currentUser.rentalId);
      await updateDoc(rentalRef, {
        status: 'returned',
        returnedAt: serverTimestamp(),
        processedBy: admin.uid,
        returnStatus: 'damaged',
        penaltyPoints: penaltyPoints,
        penaltyReason: penaltyReason
      });
      
      // 2.    
      const userRef = doc(db, 'user_profiles', currentUser.id);
      const userDoc = await getDoc(userRef);
      
      if (userDoc.exists()) {
        const currentPenalty = userDoc.data().penaltyPoints || 0;
        await updateDoc(userRef, {
          penaltyPoints: currentPenalty + penaltyPoints,
          penaltyHistory: [
            ...(userDoc.data().penaltyHistory || []),
            {
              points: penaltyPoints,
              reason: penaltyReason,
              date: serverTimestamp(),
              rentalId: currentUser.rentalId,
              adminId: admin.uid
            }
          ]
        });
      }
      
      // 3.   
      const rentalDoc = await getDoc(rentalRef);
      const equipmentId = rentalDoc.data().equipmentId;
      
      if (equipmentId) {
        const equipmentRef = doc(db, 'cameras', equipmentId);
        const equipmentDoc = await getDoc(equipmentRef);
        
        if (equipmentDoc.exists()) {
          await updateDoc(equipmentRef, {
            status: 'available',
            lastRentalId: null,
            damageHistory: [
              ...(equipmentDoc.data().damageHistory || []),
              {
                date: serverTimestamp(),
                description: penaltyReason,
                rentalId: currentUser.rentalId,
                userId: currentUser.id
              }
            ]
          });
        }
      }
      
      alert(`   ${penaltyPoints}  .`);
      setPenaltyModalOpen(false);
      setPenaltyPoints(0);
      setPenaltyReason('');
      setCurrentUser(null);
      fetchRentalData();
    } catch (error) {
      console.error('Error applying penalty:', error);
      alert('   .');
    }
  };

  //   
  const processSelectedReturns = async () => {
    if (selectedItems.length === 0) {
      alert('  .');
      return;
    }
    
    if (!confirm(` ${selectedItems.length}    ?`)) return;
    
    try {
      const batch = writeBatch(db);
      
      for (const rentalId of selectedItems) {
        const rentalRef = doc(db, 'reservations', rentalId);
        batch.update(rentalRef, {
          status: 'returned',
          returnedAt: serverTimestamp(),
          processedBy: admin.uid,
          returnStatus: 'normal'
        });
      }
      
      await batch.commit();
      
      //   
      for (const rentalId of selectedItems) {
        const rentalRef = doc(db, 'reservations', rentalId);
        const rentalDoc = await getDoc(rentalRef);
        const equipmentId = rentalDoc.data().equipmentId;
        
        if (equipmentId) {
          const equipmentRef = doc(db, 'cameras', equipmentId);
          await updateDoc(equipmentRef, {
            status: 'available',
            lastRentalId: null
          });
        }
      }
      
      alert(`${selectedItems.length}   .`);
      setSelectedItems([]);
      fetchRentalData();
    } catch (error) {
      console.error('Error batch processing returns:', error);
      alert('    .');
    }
  };

  //   
  const toggleExpand = (itemId) => {
    setExpandedItems(prev => ({
      ...prev,
      [itemId]: !prev[itemId]
    }));
  };

  //  
  const formatDate = (dateString, timeString) => {
    if (!dateString) return 'N/A';
    const formattedDate = dateString;
    return timeString ? `${formattedDate} ${timeString}` : formattedDate;
  };

  //  
  const renderPenaltyModal = () => {
    if (!penaltyModalOpen) return null;
    
    return (
      <div style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        zIndex: 1000
      }}>
        <div style={{
          backgroundColor: 'white',
          padding: '30px',
          borderRadius: '10px',
          width: '500px'
        }}>
          <h3 style={{ marginTop: 0, marginBottom: '20px' }}> </h3>
          <p><strong>:</strong> {currentUser?.name}</p>
          
          <div style={{ marginBottom: '15px' }}>
            <label style={{ display: 'block', marginBottom: '5px' }}> (1-10):</label>
            <input 
              type="number" 
              min="1"
              max="10"
              value={penaltyPoints} 
              onChange={(e) => setPenaltyPoints(parseInt(e.target.value))}
              style={{
                width: '100%',
                padding: '8px',
                border: '1px solid #ccc',
                borderRadius: '4px'
              }}
            />
          </div>
          
          <div style={{ marginBottom: '20px' }}>
            <label style={{ display: 'block', marginBottom: '5px' }}>:</label>
            <textarea 
              value={penaltyReason} 
              onChange={(e) => setPenaltyReason(e.target.value)}
              style={{
                width: '100%',
                padding: '8px',
                border: '1px solid #ccc',
                borderRadius: '4px',
                minHeight: '100px'
              }}
              placeholder="    ."
            />
          </div>
          
          <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '10px' }}>
            <button 
              onClick={() => setPenaltyModalOpen(false)}
              style={{
                padding: '8px 15px',
                border: '1px solid #ccc',
                borderRadius: '4px',
                backgroundColor: '#f5f5f5'
              }}
            >
              
            </button>
            <button 
              onClick={applyPenaltyAndProcessReturn}
              style={{
                padding: '8px 15px',
                backgroundColor: '#e53935',
                color: 'white',
                border: 'none',
                borderRadius: '4px'
              }}
            >
                  
            </button>
          </div>
        </div>
      </div>
    );
  };

  //   
  const renderSortControls = () => {
    return (
      <div style={{ 
        display: 'flex', 
        alignItems: 'center',
        marginBottom: '15px',
        padding: '10px',
        backgroundColor: '#f5f5f5',
        borderRadius: '4px'
      }}>
        <span style={{ marginRight: '15px', fontWeight: 'bold' }}>:</span>
        <div style={{ display: 'flex', gap: '10px' }}>
          <button 
            onClick={() => handleSortChange('userName')}
            style={{
              padding: '5px 10px',
              backgroundColor: sortBy === 'userName' ? '#2196F3' : '#E0E0E0',
              color: sortBy === 'userName' ? 'white' : 'black',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            
          </button>
          <button 
            onClick={() => handleSortChange('userId')}
            style={{
              padding: '5px 10px',
              backgroundColor: sortBy === 'userId' ? '#2196F3' : '#E0E0E0',
              color: sortBy === 'userId' ? 'white' : 'black',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            ID
          </button>
          <button 
            onClick={() => handleSortChange('rentalDate')}
            style={{
              padding: '5px 10px',
              backgroundColor: sortBy === 'rentalDate' ? '#2196F3' : '#E0E0E0',
              color: sortBy === 'rentalDate' ? 'white' : 'black',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            
          </button>
          <button 
            onClick={() => handleSortChange('name')}
            style={{
              padding: '5px 10px',
              backgroundColor: sortBy === 'name' ? '#2196F3' : '#E0E0E0',
              color: sortBy === 'name' ? 'white' : 'black',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            
          </button>
        </div>
      </div>
    );
  };

  //   
  const renderRentalItem = (item, type) => {
    const isExpanded = expandedItems[item.id] || false;
    const isSelected = selectedItems.includes(item.id);
    
    return (
      <div 
        key={item.id} 
        style={{
          border: '1px solid #E0E0E0',
          borderRadius: '8px',
          padding: '15px',
          marginBottom: '15px',
          backgroundColor: isSelected ? '#f0f7ff' : '#fff'
        }}
      >
        <div style={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          marginBottom: '10px'
        }}>
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <input 
              type="checkbox"
              checked={isSelected}
              onChange={() => toggleSelectItem(item.id)}
              style={{ marginRight: '10px' }}
            />
            <h3 style={{ 
              fontSize: '18px', 
              fontWeight: 'bold',
              cursor: 'pointer',
              margin: 0
            }}
            onClick={() => toggleExpand(item.id)}
            >
              {item.name}
            </h3>
          </div>
          
          <div style={{ display: 'flex', alignItems: 'center' }}>
            {type === 'pending' && (
              <>
                <button 
                  onClick={() => approveRental(item.id)}
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '5px',
                    padding: '5px 10px',
                    backgroundColor: '#4caf50',
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    marginRight: '10px',
                    cursor: 'pointer'
                  }}
                >
                  <Check size={16} />
                  
                </button>
                <button 
                  onClick={() => rejectRental(item.id)}
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '5px',
                    padding: '5px 10px',
                    backgroundColor: '#f44336',
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    marginRight: '10px',
                    cursor: 'pointer'
                  }}
                >
                  <X size={16} />
                  
                </button>
              </>
            )}
            
            {type === 'return' && (
              <button 
                onClick={() => processReturn(item.id)}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  gap: '5px',
                  padding: '5px 10px',
                  backgroundColor: '#2196f3',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  marginRight: '10px',
                  cursor: 'pointer'
                }}
              >
                <RefreshCw size={16} />
                 
              </button>
            )}
            
            <button 
              onClick={() => toggleExpand(item.id)}
              style={{
                display: 'flex',
                alignItems: 'center',
                padding: '5px',
                background: 'none',
                border: 'none',
                cursor: 'pointer'
              }}
            >
              {isExpanded ? <ChevronUp size={18} /> : <ChevronDown size={18} />}
            </button>
          </div>
        </div>
        
        {isExpanded && (
          <div style={{ 
            padding: '10px', 
            backgroundColor: '#f9f9f9', 
            borderRadius: '4px',
            marginTop: '10px'
          }}>
            <div style={{ display: 'flex', marginBottom: '15px' }}>
            <div style={{ flex: '0 0 200px', marginRight: '20px' }}>
                {imageUrls[item.id] ? (
                  <img 
                    src={imageUrls[item.id]} 
                    alt={item.name} 
                    style={{ 
                      width: '180px', 
                      height: '180px', 
                      objectFit: 'cover',
                      borderRadius: '4px' 
                    }} 
                  />
                ) : (
                  <div style={{ 
                    width: '180px', 
                    height: '180px', 
                    backgroundColor: '#E0E0E0',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    borderRadius: '4px'
                  }}>
                    <span> </span>
                  </div>
                )}
              </div>
              
              <div style={{ flex: '1' }}>
                <p><strong>:</strong> {item.userName || ' '}</p>
                <p><strong> ID:</strong> {item.userId}</p>
                <p><strong>:</strong> {item.userEmail || ' '}</p>
                <p><strong> :</strong> {formatDate(item.rentalDate, item.rentalTime)}</p>
                <p><strong> :</strong> {formatDate(item.returnDate, item.returnTime)}</p>
                {item.purpose && <p><strong> :</strong> {item.purpose}</p>}
                {item.status === 'active' && (
                  <p><strong> :</strong> {item.rentalPeriod || '1'}</p>
                )}
                
                {item.penaltyPoints > 0 && (
                  <p style={{ color: '#f44336' }}>
                    <strong>:</strong> {item.penaltyPoints} ({item.penaltyReason})
                  </p>
                )}
              </div>
            </div>
            
            {item.notes && (
              <div style={{ marginTop: '10px', padding: '10px', backgroundColor: '#fffde7', borderRadius: '4px' }}>
                <p><strong> :</strong></p>
                <p>{item.notes}</p>
              </div>
            )}
          </div>
        )}
      </div>
    );
  };

  //   
  const renderTabContent = () => {
    switch (activeTab) {
      case 'pending':
        return (
          <>
            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '15px' }}>
              <div>
                <h3>   ({pendingRentals.length})</h3>
              </div>
              <div style={{ display: 'flex', gap: '10px' }}>
                <button 
                  onClick={() => toggleSelectAll(pendingRentals)}
                  style={{
                    padding: '5px 10px',
                    backgroundColor: '#E0E0E0',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer'
                  }}
                >
                  {selectedItems.length === pendingRentals.length && pendingRentals.length > 0 ? ' ' : ' '}
                </button>
                
                <button 
                  onClick={approveSelectedRentals}
                  disabled={selectedItems.length === 0}
                  style={{
                    padding: '5px 10px',
                    backgroundColor: selectedItems.length > 0 ? '#4caf50' : '#E0E0E0',
                    color: selectedItems.length > 0 ? 'white' : '#9E9E9E',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: selectedItems.length > 0 ? 'pointer' : 'not-allowed'
                  }}
                >
                    
                </button>
                
                <button 
                  onClick={rejectSelectedRentals}
                  disabled={selectedItems.length === 0}
                  style={{
                    padding: '5px 10px',
                    backgroundColor: selectedItems.length > 0 ? '#f44336' : '#E0E0E0',
                    color: selectedItems.length > 0 ? 'white' : '#9E9E9E',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: selectedItems.length > 0 ? 'pointer' : 'not-allowed'
                  }}
                >
                    
                </button>
              </div>
            </div>
            
            {renderSortControls()}
            
            {pendingRentals.length > 0 ? (
              pendingRentals.map(item => renderRentalItem(item, 'pending'))
            ) : (
              <div style={{ 
                padding: '30px', 
                textAlign: 'center', 
                backgroundColor: '#f5f5f5',
                borderRadius: '8px'
              }}>
                <p>    .</p>
              </div>
            )}
          </>
        );
        
      case 'active':
        return (
          <>
            <h3>    ({activeRentals.length})</h3>
            {activeRentals.length > 0 ? (
              activeRentals.map(item => renderRentalItem(item, 'active'))
            ) : (
              <div style={{ 
                padding: '30px', 
                textAlign: 'center', 
                backgroundColor: '#f5f5f5',
                borderRadius: '8px'
              }}>
                <p>    .</p>
              </div>
            )}
          </>
        );
        
      case 'return':
        return (
          <>
            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '15px' }}>
              <div>
                <h3>  ({returnRequests.length})</h3>
              </div>
              <div style={{ display: 'flex', gap: '10px' }}>
                <button 
                  onClick={() => toggleSelectAll(returnRequests)}
                  style={{
                    padding: '5px 10px',
                    backgroundColor: '#E0E0E0',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer'
                  }}
                >
                  {selectedItems.length === returnRequests.length && returnRequests.length > 0 ? ' ' : ' '}
                </button>
                
                <button 
                  onClick={processSelectedReturns}
                  disabled={selectedItems.length === 0}
                  style={{
                    padding: '5px 10px',
                    backgroundColor: selectedItems.length > 0 ? '#2196f3' : '#E0E0E0',
                    color: selectedItems.length > 0 ? 'white' : '#9E9E9E',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: selectedItems.length > 0 ? 'pointer' : 'not-allowed'
                  }}
                >
                      
                </button>
              </div>
            </div>
            
            {returnRequests.length > 0 ? (
              returnRequests.map(item => renderRentalItem(item, 'return'))
            ) : (
              <div style={{ 
                padding: '30px', 
                textAlign: 'center', 
                backgroundColor: '#f5f5f5',
                borderRadius: '8px'
              }}>
                <p>     .</p>
              </div>
            )}
          </>
        );
        
      default:
        return null;
    }
  };

  //  
  if (loading) {
    return (
      <div style={{ 
        display: 'flex', 
        justifyContent: 'center', 
        alignItems: 'center',
        height: '100vh'
      }}>
        <p> ...</p>
      </div>
    );
  }

  //  
  return (
    <div style={{ maxWidth: '1200px', margin: '0 auto', padding: '20px' }}>
      {/* Header */}
      <div style={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center',
        marginBottom: '20px',
        padding: '15px',
        backgroundColor: '#1976d2',
        color: 'white',
        borderRadius: '8px'
      }}>
        <h1 style={{ margin: 0 }}> </h1>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <User size={18} style={{ marginRight: '5px' }} />
          <span>{admin?.name || ''}</span>
        </div>
      </div>
      
      {/* Navigation */}
      <div style={{ 
        display: 'flex', 
        marginBottom: '20px', 
        backgroundColor: '#f5f5f5',
        borderRadius: '8px',
        overflow: 'hidden'
      }}>
        <button 
          onClick={handleHomeNavigation}
          style={{ 
            flex: '1',
            padding: '12px',
            border: 'none',
            backgroundColor: 'transparent',
            cursor: 'pointer',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            gap: '5px'
          }}
        >
          <Home size={20} />
          <span></span>
        </button>
        
        <button 
          onClick={handleCalendarNavigation}
          style={{ 
            flex: '1',
            padding: '12px',
            border: 'none',
            backgroundColor: 'transparent',
            cursor: 'pointer',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            gap: '5px'
          }}
        >
          <Calendar size={20} />
          <span></span>
        </button>
        
        <button 
          onClick={handleNoteNavigation}
          style={{ 
            flex: '1',
            padding: '12px',
            border: 'none',
            backgroundColor: 'transparent',
            cursor: 'pointer',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            gap: '5px'
          }}
        >
          <FileText size={20} />
          <span></span>
        </button>
        
        <button 
          onClick={handleReservateNavigation}
          style={{ 
            flex: '1',
            padding: '12px',
            border: 'none',
            backgroundColor: 'transparent',
            cursor: 'pointer',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            gap: '5px'
          }}
        >
          <ShoppingCart size={20} />
          <span></span>
        </button>
      </div>
      
      {/* Tab Navigation */}
      <div style={{ 
        display: 'flex', 
        marginBottom: '20px',
        borderBottom: '1px solid #E0E0E0'
      }}>
        <button 
          onClick={() => {
            setActiveTab('pending');
            setSelectedItems([]);
          }}
          style={{ 
            padding: '10px 20px',
            border: 'none',
            backgroundColor: 'transparent',
            borderBottom: activeTab === 'pending' ? '3px solid #1976d2' : 'none',
            color: activeTab === 'pending' ? '#1976d2' : '#616161',
            fontWeight: activeTab === 'pending' ? 'bold' : 'normal',
            cursor: 'pointer'
          }}
        >
            
        </button>
        
        <button 
          onClick={() => {
            setActiveTab('active');
            setSelectedItems([]);
          }}
          style={{ 
            padding: '10px 20px',
            border: 'none',
            backgroundColor: 'transparent',
            borderBottom: activeTab === 'active' ? '3px solid #1976d2' : 'none',
            color: activeTab === 'active' ? '#1976d2' : '#616161',
            fontWeight: activeTab === 'active' ? 'bold' : 'normal',
            cursor: 'pointer'
          }}
        >
            
        </button>
        
        <button 
          onClick={() => {
            setActiveTab('return');
            setSelectedItems([]);
          }}
          style={{ 
            padding: '10px 20px',
            border: 'none',
            backgroundColor: 'transparent',
            borderBottom: activeTab === 'return' ? '3px solid #1976d2' : 'none',
            color: activeTab === 'return' ? '#1976d2' : '#616161',
            fontWeight: activeTab === 'return' ? 'bold' : 'normal',
            cursor: 'pointer'
          }}
        >
            
        </button>
      </div>
      
      {/* Main Content */}
      <div style={{ backgroundColor: 'white', padding: '20px', borderRadius: '8px' }}>
        {renderTabContent()}
      </div>
      
      {/* Penalty Modal */}
      {renderPenaltyModal()}
      
      {/* Footer */}
      <div style={{ marginTop: '30px', textAlign: 'center', color: '#757575', fontSize: '14px' }}>
        <p> 2025    </p>
      </div>
    </div>
  );
};

export default AdminPage;