import React, { useState, useEffect } from 'react';
import { 
  collection, 
  addDoc, 
  getDocs, 
  deleteDoc, 
  doc, 
  updateDoc 
} from 'firebase/firestore';
import { 
  ref, 
  uploadBytes, 
  getDownloadURL,
  deleteObject
} from 'firebase/storage';
import { db, storage } from '../firebase/firebaseConfig';

const processImage = (file) => {
  return new Promise((resolve, reject) => {
    const img = new Image();
    const reader = new FileReader();
    reader.onload = (e) => {
      img.onload = () => {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        
        //     (: 800x600)
        const maxWidth = 800;
        const maxHeight = 600;
        
        //    
        let width = img.width * 0.8;
        let height = img.height * 0.8;
        
        //   
        if (width > height) {
          if (width > maxWidth) {
            height *= maxWidth / width;
            width = maxWidth;
          }
        } else {
          if (height > maxHeight) {
            width *= maxHeight / height;
            height = maxHeight;
          }
        }
        
        //   
        canvas.width = img.width;
        canvas.height = img.height;
        
        //   
        ctx.fillStyle = '#FFFFFF';
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        
        //   70%  
        const x = (canvas.width - width) / 2;
        const y = (canvas.height - height) / 2;
        
        ctx.drawImage(img, x, y, width, height);
        
        //   
        const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
        const data = imageData.data;
        
        //    
        for (let i = 0; i < data.length; i += 4) {
          if (
            data[i] > 240 && 
            data[i+1] > 240 && 
            data[i+2] > 240
          ) {
            data[i+3] = 0; //  
          }
        }
        
        //     
        ctx.putImageData(imageData, 0, 0);
        
        // Base64 
        canvas.toBlob((blob) => {
          resolve(new File([blob], file.name, { 
            type: 'image/png',
            lastModified: Date.now()
          }));
        }, 'image/png');
      };
      img.src = e.target.result;
    };
    reader.onerror = reject;
    reader.readAsDataURL(file);
  });
};

const AdminEquipmentManagement = () => {
  const [equipments, setEquipments] = useState([]);
  const [newEquipment, setNewEquipment] = useState({
    name: '',
    category: '',
    brand: '',
    issues: '',
    purpose: '',
    description: '',
    status: 'available',
    condition: '',
    dailyRentalPrice: '',
    image: ''
  });
  const [imageFile, setImageFile] = useState(null);
  const [editingEquipment, setEditingEquipment] = useState(null);

  useEffect(() => {
    fetchEquipments();
  }, []);

  const fetchEquipments = async () => {
    try {
      const equipmentCollection = collection(db, 'cameras');
      const snapshot = await getDocs(equipmentCollection);
      const equipmentList = snapshot.docs.map(d => ({
        id: d.id, 
        ...d.data()
      }));
      setEquipments(equipmentList);
    } catch (error) {
      console.error("    :", error);
      alert('     .');
    }
  };

  const handleImageUpload = async () => {
    if (!imageFile) return null;
    try {
      //  
      const processedFile = await processImage(imageFile);
      
      const uniqueFileName = `${Date.now()}_${processedFile.name}`;
      const storageRef = ref(storage, `camera-images/${uniqueFileName}`);
      
      const allowedTypes = ['image/jpeg', 'image/png', 'image/gif'];
      if (!allowedTypes.includes(processedFile.type)) {
        alert('   . JPEG, PNG, GIF   .');
        return null;
      }
      
      const maxSize = 5 * 1024 * 1024;
      if (processedFile.size > maxSize) {
        alert('   . 5MB    .');
        return null;
      }
      
      const snapshot = await uploadBytes(storageRef, processedFile);
      const downloadURL = await getDownloadURL(snapshot.ref);
      return { downloadURL, storageRef };
    } catch (error) {
      console.error("   :", error);
      alert('  .');
      return null;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      let imageUrl = null;
  
if (imageFile) {
  const uploadResult = await handleImageUpload();
  if (uploadResult) {
    imageUrl = uploadResult.downloadURL;
    
    //    
    if (editingEquipment && editingEquipment.image) {
      try {
        const existingImageRef = ref(storage, editingEquipment.image);
        await deleteObject(existingImageRef);
      } catch (deleteError) {
        console.error("    :", deleteError);
      }
    }
  } else {
    return; //      
  }
} else if (editingEquipment) {
  //        
  imageUrl = editingEquipment.image;
}

      const equipmentData = {
        ...newEquipment,
        image: imageUrl || (editingEquipment ? editingEquipment.image : ''),
        createdAt: editingEquipment ? editingEquipment.createdAt : new Date()
      };

      if (editingEquipment) {
        await updateDoc(doc(db, 'cameras', editingEquipment.id), equipmentData);
        setEditingEquipment(null);
      } else {
        await addDoc(collection(db, 'cameras'), equipmentData);
      }

      fetchEquipments();
      
      // Reset form
      setNewEquipment({
        name: '',
        category: '',
        brand: '',
        issues: '',
        purpose: '',
        description: '',
        status: 'available',
        condition: '',
        dailyRentalPrice: '',
        image: ''
      });
      setImageFile(null);
    } catch (error) {
      console.error(" /  :", error);
      alert(' /   .');
    }
  };

  const handleDelete = async (equipment) => {
    try {
      if (equipment.image) {
        const imageRef = ref(storage, equipment.image);
        await deleteObject(imageRef);
      }
      
      await deleteDoc(doc(db, 'cameras', equipment.id));
      fetchEquipments();
    } catch (error) {
      console.error("   :", error);
      alert('    .');
    }
  };

  const handleEdit = (equipment) => {
    setEditingEquipment(equipment);
    setNewEquipment({
      name: equipment.name,
      category: equipment.category,
      brand: equipment.brand || '',
      issues: equipment.issues || '',
      purpose: equipment.purpose || '',
      description: equipment.description || '',
      status: equipment.status || 'available',
      condition: equipment.condition || '',
      dailyRentalPrice: equipment.dailyRentalPrice || '',
      image: equipment.image || ''
    });
  };

  return (
    <div style={{
      fontFamily: 'Pretendard, sans-serif',
      maxWidth: '1200px',
      margin: '0 auto',
      padding: '20px',
      backgroundColor: 'white',
      color: 'black',
      display: 'flex'
    }}>
      <div style={{ width: '40%', marginRight: '20px' }}>
        <h1 style={{ 
          fontSize: '24px', 
          marginBottom: '20px', 
          borderBottom: '2px solid black',
          color: 'black' 
        }}>
            
        </h1>

        <form 
          onSubmit={handleSubmit}
          style={{
            display: 'grid',
            gap: '15px',
            marginBottom: '30px',
            color: 'black'
          }}
        >
          <input
            type="text"
            placeholder=" "
            value={newEquipment.name}
            onChange={(e) => setNewEquipment({...newEquipment, name: e.target.value})}
            style={{
              padding: '10px',
              border: '1px solid #333',
              borderRadius: '5px',
              backgroundColor: 'white',
              color: 'black'
            }}
            required
          />
          
          <select
            value={newEquipment.category}
            onChange={(e) => setNewEquipment({...newEquipment, category: e.target.value})}
            style={{
              padding: '10px',
              border: '1px solid #333',
              borderRadius: '5px',
              backgroundColor: 'white',
              color: 'black'
            }}
            required
          >
            <option value="">  </option>
            <option value="Filming"> </option>
            <option value="Lighting"> </option>
            <option value="Battery"></option>
            <option value="Sound"> </option>
            <option value="VR device">VR </option>
            <option value="ETC"></option>
          </select>

          <input
            type="text"
            placeholder=""
            value={newEquipment.brand}
            onChange={(e) => setNewEquipment({...newEquipment, brand: e.target.value})}
            style={{
              padding: '10px',
              border: '1px solid #333',
              borderRadius: '5px',
              backgroundColor: 'white',
              color: 'black'
            }}
          />

          <textarea
            placeholder=" "
            value={newEquipment.issues}
            onChange={(e) => setNewEquipment({...newEquipment, issues: e.target.value})}
            style={{
              padding: '10px',
              border: '1px solid #333',
              borderRadius: '5px',
              minHeight: '100px',
              backgroundColor: 'white',
              color: 'black'
            }}
          />

          <textarea
            placeholder=" "
            value={newEquipment.description}
            onChange={(e) => setNewEquipment({...newEquipment, description: e.target.value})}
            style={{
              padding: '10px',
              border: '1px solid #333',
              borderRadius: '5px',
              minHeight: '100px',
              backgroundColor: 'white',
              color: 'black'
            }}
          />

          <select
            value={newEquipment.status}
            onChange={(e) => setNewEquipment({...newEquipment, status: e.target.value})}
            style={{
              padding: '10px',
              border: '1px solid #333',
              borderRadius: '5px',
              backgroundColor: 'white',
              color: 'black'
            }}
          >
            <option value="available"> </option>
            <option value="rented"> </option>
          </select>

          <select
            value={newEquipment.condition}
            onChange={(e) => setNewEquipment({...newEquipment, condition: e.target.value})}
            style={{
              padding: '10px',
              border: '1px solid #333',
              borderRadius: '5px',
              backgroundColor: 'white',
              color: 'black'
            }}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>

          <input
            type="file"
            onChange={(e) => setImageFile(e.target.files[0])}
            style={{
              padding: '10px',
              border: '1px solid #333',
              borderRadius: '5px',
              backgroundColor: 'white',
              color: 'black'
            }}
          />

          <button 
            type="submit"
            style={{
              padding: '10px',
              backgroundColor: '#333',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer'
            }}
          >
            {editingEquipment ? ' ' : ' '}
          </button>
        </form>
      </div>

      <div style={{ width: '60%' }}>
        <h2 style={{ 
          fontSize: '20px', 
          marginBottom: '20px', 
          borderBottom: '2px solid black',
          color: 'black' 
        }}>
           
        </h2>
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(2, 1fr)',
          gap: '20px',
          color: 'black',
          maxHeight: '600px',
          overflowY: 'auto'
        }}>
          {equipments.map((equipment) => (
            <div 
              key={equipment.id}
              style={{
                border: '1px solid #333',
                borderRadius: '10px',
                padding: '15px',
                textAlign: 'center',
                backgroundColor: 'white'
              }}
            >
              {equipment.image && (
                <img 
                  src={equipment.image} 
                  alt={equipment.name}
                  style={{
                    width: '100%',
                    height: '200px',
                    objectFit: 'cover',
                    borderRadius: '10px',
                    marginBottom: '10px'
                  }}
                />
              )}
              <h3 style={{ marginBottom: '10px', color: 'black' }}>{equipment.name}</h3>
              <p style={{ color: 'black' }}>: {equipment.category}</p>
              <p style={{ color: 'black' }}>: {equipment.status === 'available' ? ' ' : ' '}</p>
              <p style={{ color: 'black' }}>: {equipment.brand || ''}</p>
              <p style={{ color: 'black' }}> : {equipment.issues || ''}</p>
              <p style={{ color: 'black' }}> : {equipment.condition}</p>
              <div style={{ display: 'flex', justifyContent: 'center', gap: '10px', marginTop: '10px' }}>
                <button
                  onClick={() => handleEdit(equipment)}
                  style={{
                    padding: '5px 10px',
                    backgroundColor: '#333',
                    color: 'white',
                    border: 'none',
                    borderRadius: '5px',
                    cursor: 'pointer'
                  }}
                >
                  
                </button>
                <button
                  onClick={() => handleDelete(equipment)}
                  style={{
                    padding: '5px 10px',
                    backgroundColor: 'red',
                    color: 'white',
                    border: 'none',
                    borderRadius: '5px',
                    cursor: 'pointer'
                  }}
                >
                  
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default AdminEquipmentManagement;