import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { auth, signInWithPopup, provider } from "../firebase/firebaseConfig";

function MainHeader({ scrollToSection, refs }) {
  
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userName, setUserName] = useState('');
  const [isVisible, setIsVisible] = useState(false);
  
  const navigate = useNavigate();

  //   
  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (user) {
        setIsLoggedIn(true);
        setUserName(user.displayName || user.email.split('@')[0]);
      } else {
        setIsLoggedIn(false);
        setUserName('');
      }
    });
    return () => unsubscribe();
  }, []);

  //       
  useEffect(() => {
    const toggleVisibility = () => {
      //   300px    
      if (window.pageYOffset > 300) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }
    };

    //    
    window.addEventListener('scroll', toggleVisibility);

    //      
    return () => window.removeEventListener('scroll', toggleVisibility);
  }, []);

  //   
  const handleLogin = async () => {
    try {
      await signInWithPopup(auth, provider);
      alert("");
    } catch (error) {
      console.error(" : ", error.message);
    }
  };

  //   
  const handleLogout = async () => {
    try {
      await auth.signOut();
      alert(".");
      setIsLoggedIn(false);
    } catch (error) {
      console.error(" : ", error.message);
    }
  };

  //    
  const handleReservation = () => {
    if (!isLoggedIn) {
      //       
      alert("   .");
      return;
    }
    navigate('/ReservationMainPage');
    setTimeout(() => {
      window.scrollTo(0, 0);
      window.location.reload();
    }, 100);
  };

  //    
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };

  return (
    <div style={{
      position: 'relative',
      width: '100vw',
      height: '100vh',
      backgroundColor: 'black',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      overflow: 'hidden',
      border: '0px solid black'
    }}>
      <div style={{
        minWidth: '1440px',
        height: '1080px',
        backgroundColor: '#F1F1F1',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        position: 'relative',
        margin: 'auto',
        overflow: 'hidden',
      }}>
        {/* KHDC    */}
        <div style={{
          position: 'absolute',
          top: '20px',
          left: '10px',
          display: 'flex',
          alignItems: 'center',
          gap: '8px'
        }}>
          <img src="/assets/KHDC.png" alt="KHDC Logo" style={{
            width: '96px',
            height: '54px',
            objectFit: 'contain',
            display: 'block',
            margin: '0',
            padding: '0',
          }} />
          <span style={{
            position: 'absolute',
            left: "81px",
            color: 'black',
            fontSize: '16px',
            fontWeight: '500',
            whiteSpace: 'nowrap'
          }}>
            Kyunghee Digital contents
          </span>
        </div>

        {/* /  */}
        <div style={{
          position: 'absolute',
          top: '40px',
          right: '60px',
          color: 'black',
          fontSize: '18px',
          fontWeight: '400',
          cursor: 'pointer',
          zIndex: '1',
          display: 'flex',
          alignItems: 'center',
          gap: '10px'
        }}>
           {isLoggedIn && (
          <span style={{
            marginRight: '10px',
            fontWeight: '400'
          }}>
            {userName}
          </span>
        )}

          {isLoggedIn ? (
            <span onClick={handleLogout}>Log out</span>
          ) : (
            <span onClick={handleLogin}>Log in</span>
          )}
        </div>

        {/*   */}
        <div style={{
          position: 'absolute',
          top: '14%',
          left: '51%',
          transform: 'translate(-50%, -50%)',
          color: '#CCCCCC',
          fontSize: '190px',
          fontWeight: 'bold',
          opacity: '0.4',
          zIndex: '0',
          whiteSpace: 'nowrap'
        }}>
          Equipment Rental
        </div>

        {/*   */}
        <div style={{
          position: "absolute",
          top:"160px",
          textAlign: 'right',
          marginLeft: '670px',
          marginBottom: '15px',
          zIndex: '1'
        }}>
          <h1 style={{
            color: 'black',
            fontSize: '170px',
            fontWeight: '900',
            lineHeight: '115px',
            textAlign: 'right'
          }}>
             
          </h1>
        </div>
        <div style={{
          position:"absolute",
          top:"288px",
          textAlign: 'right',
          marginLeft: '61px',
          zIndex: '1'
        }}>
          <h1 style={{
            color: 'black',
            fontSize: '170px',
            fontWeight: '900',
            lineHeight: '180px'
          }}>
             
          </h1>
        </div>

        {/*  */}
        <div style={{
          position: "absolute",
          marginLeft: '200px',
          marginTop: '80px'
        }}>
          <h2 style={{
            color: 'black',
            fontSize: '90px',
            fontWeight: '100',
            lineHeight: '110px'
          }}>
            Department of Digital <br /> Contents
          </h2>
        </div>

        {/*  */}
        <hr style={{
          position: 'absolute',
          width: '820px',
          height: '3px',
          backgroundColor: 'black',
          marginLeft: '380px',
          marginTop: '100px'
        }} />

        {/*   */}
        <div style={{
          marginLeft: '0px',
          marginTop: '100px'
        }}>
          <p style={{
            position: 'absolute',
            color: 'black',
            fontSize: '18px',
            textAlign: 'left',
            marginLeft: '-650px',
            marginTop: '200px',
            fontWeight: '200'
          }}>
            "", ""      .<br />
                 .
          </p>
        </div>

        {/*   */}
<div style={{
  marginTop: '40px',
  display: 'flex',
  gap: '20px',
  justifyContent: 'center'
}}>
  <button 
    onClick={handleReservation}
    style={{
      position: 'absolute',
      right: '370px',
      top: '765px',
      backgroundColor: '#D3D3D3',
      color: 'black',
      padding: '5px 13px',
      borderRadius: '8px',
      fontSize: '30px',
      fontWeight: '700',
      display: 'flex',
      alignItems: 'center',
      gap: '4px'
    }}
  >
    <img src="/assets/CheckMark.png" alt="Reserve Icon" style={{
      width: '40px',
      height: '40px'
    }} />
    
  </button>
  <button onClick={() => scrollToSection(refs.calendar)} style={{
    position: 'absolute',
    backgroundColor: '#D3D3D3',
    color: 'black',
    padding: '5px 13px',
    borderRadius: '8px',
    fontSize: '30px',
    fontWeight: '700',
    display: 'flex',
    alignItems: 'center',
    gap: '8px',
    marginRight: "-900px",
    top: "765px"
  }}>
    <img src="/assets/Calendar.png" alt="Calendar Icon" style={{
      width: '35px',
      height: '35px'
    }} />
    
  </button>
</div>

        {/*     */}
        {isVisible && (
          <button 
            onClick={scrollToTop}
            style={{
              position: 'fixed',
              bottom: '40px',
              right: '40px',
              backgroundColor: 'rgba(211, 211, 211, 0.8)', //   
              color: 'black',
              borderRadius: '50%', //  
              width: '60px',
              height: '60px',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              border: 'none',
              boxShadow: '0 4px 6px rgba(0,0,0,0.1)', //  
              cursor: 'pointer',
              zIndex: 1000 //    
            }}
          >
             {/*   */}
          </button>
        )}
      </div>
    </div>
  );
}

export default MainHeader;