import React, { useState, useEffect } from 'react';
import { User, ShoppingCart, Trash2, CheckCircle } from 'lucide-react';
import { useLocation, useNavigate } from 'react-router-dom';
import {
  db, 
  app,
  getAuth,
  updateDoc,
  doc,
  getDoc,
  getImageURL,
  setDoc
} from '../firebase/firebaseConfig';

const CartPage = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [cartItems, setCartItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [imageUrls, setImageUrls] = useState({});
  const [user, setUser] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  //   ID (  )
  const ADMIN_CALENDAR_ID = process.env.REACT_APP_CALENDAR_ID || '837ac43ba185f6e8b56e97f1f7e15ecbb103bc44c111e6b8c81fe28ec713b8e9@group.calendar.google.com';
  
  // API  (  URL  )
  const API_ENDPOINT = process.env.REACT_APP_API_ENDPOINT || '/api/calendar';

  //  
  const handleHomeNavigation = () => {
    navigate('/main');
  };

  const handleCalendarNavigation = () => {
    navigate('/calendar', { state: { scrollTo: 'calendar-section' } });
  };

  const handleNoteNavigation = () => {
    navigate('/thingsnote', { state: { scrollTo: 'notes-section' } });
  };

  const handleMypageNavigation = () => {
    navigate('/mypage');
  };
  
  const handleReservateNavigation = () => {
    navigate('/ReservationMainPage');
  };

  // Firebase    
  const fetchFirebaseCartItems = async (userId) => {
    try {
      const userCartRef = doc(db, 'user_carts', userId);
      const cartDoc = await getDoc(userCartRef);
      
      console.log('Firebase Cart Document:', cartDoc.exists());

      if (cartDoc.exists()) {
        const firebaseCartItems = cartDoc.data().items || [];
        console.log('Firebase Cart Items:', firebaseCartItems);
        setCartItems(firebaseCartItems);
        localStorage.setItem('cart', JSON.stringify(firebaseCartItems));
      }
      setLoading(false);
    } catch (error) {
      console.error('Firebase   :', error);
      setLoading(false);
    }
  };

  //      (  )
  const createAdminCalendarEvent = async () => {
    if (!user) {
      return alert(' .');
    }
    
    if (isSubmitting) {
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      const firstItem = cartItems[0];
      const startDateTime = `${firstItem.rentalDate}T${firstItem.rentalTime}:00`;
      const endDateTime = `${firstItem.returnDate}T${firstItem.returnTime}:00`;

      const start = new Date(`${rentalDate}T${rentalTime}`);
const end = new Date(`${returnDate}T${returnTime}`);

if (start <= reservationEnd && end >= reservationStart) {
  //   
}

      const itemListText = cartItems.map((item, index) =>
        `${index + 1}. ${item.name} (${item.category})\n: ${item.rentalDate} ${item.rentalTime} ~ : ${item.returnDate} ${item.returnTime}`
      ).join('\n');

      const eventData = {
        summary: `DIRT   - ${user.uid}`,
        description: ` ID: ${user.uid}\n\n  :\n${itemListText}`,
        startDateTime,
        endDateTime,
        calendarId: ADMIN_CALENDAR_ID,
        timeZone: 'Asia/Seoul'
      };
      
      console.log('Creating admin calendar event:', eventData);
      
      //  1:  API    (Firebase Function   )
      const response = await fetch(API_ENDPOINT, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(eventData),
      });
      
      if (!response.ok) {
        throw new Error('  .');
      }
      
      const result = await response.json();
      console.log('  :', result);
      
      //    
      if (user) {
        try {
          const userCartRef = doc(db, 'user_carts', user.uid);
          await updateDoc(userCartRef, {
            items: []
          });
        } catch (error) {
          console.error('Firebase   :', error);
        }
      }
      
      localStorage.removeItem('cart');
      setCartItems([]);
      alert('  !');
    } catch (error) {
      console.error('  :', error);
      alert('    : ' + error.message);
    } finally {
      setIsSubmitting(false);
    }
  };

  //  2:  Firebase     (  API   )
  const saveReservationToFirebase = async () => {
    if (!user) {
      return alert(' .');
    }
    
    if (isSubmitting) {
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      const firstItem = cartItems[0];
      const startDateTime = `${firstItem.rentalDate}T${firstItem.rentalTime}:00`;
      const endDateTime = `${firstItem.returnDate}T${firstItem.returnTime}:00`;
  
      // setDoc     
      const reservationId = `${user.uid}_${Date.now()}`;
      const reservationsRef = doc(db, 'reservations', reservationId);
      await setDoc(reservationsRef, {
        userId: user.uid,
        items: cartItems,
        startDateTime,
        endDateTime,
        status: 'pending', //    
        createdAt: new Date().toISOString()
      });
      
      //    
    if (user) {
      try {
        const userCartRef = doc(db, 'user_carts', user.uid);
        await updateDoc(userCartRef, {
          items: []
        });
      } catch (error) {
        console.error('Firebase   :', error);
      }
    }
    
    localStorage.removeItem('cart');
    setCartItems([]);
    alert('  !     .');
  } catch (error) {
    console.error('  :', error);
    alert('    : ' + error.message);
  } finally {
    setIsSubmitting(false);
  }
};

  //       
  useEffect(() => {
    //     
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      const parsedUser = JSON.parse(storedUser);
      setUser(parsedUser);
      console.log('Stored User:', parsedUser);
      fetchFirebaseCartItems(parsedUser.uid);
    } else {
      console.log('No user found in localStorage');
      setLoading(false);
    }
  }, []);

  //  location state   
  useEffect(() => {
    // location state    
    const passedCartItems = location.state?.cartItems;
    
    if (passedCartItems) {
      //      
      setCartItems(passedCartItems);
      localStorage.setItem('cart', JSON.stringify(passedCartItems));
      setLoading(false);
    } else if (!user) {
      //       
      const storedCart = JSON.parse(localStorage.getItem('cart')) || [];
      setCartItems(storedCart);
      setLoading(false);
    }
    // user   fetchFirebaseCartItems loading  
  }, [location.state]);

  //  
  useEffect(() => {
    const fetchImageUrls = async () => {
      const urls = {};
      for (const item of cartItems) {
        try {
          const url = await getImageURL(item.image);
          urls[item.id] = url;
        } catch (error) {
          console.error(`Error loading image for ${item.name}:`, error);
          urls[item.id] = null;
        }
      }
      setImageUrls(urls);
    };

    if (cartItems.length > 0) {
      fetchImageUrls();
    }
  }, [cartItems]);

  //   
  const removeFromCart = async (itemId) => {
    const updatedCart = cartItems.filter(item => item.id !== itemId);
    setCartItems(updatedCart);
    localStorage.setItem('cart', JSON.stringify(updatedCart));

    // Firebase  
    if (user) {
      try {
        const userCartRef = doc(db, 'user_carts', user.uid);
        await updateDoc(userCartRef, {
          items: updatedCart
        });
      } catch (error) {
        console.error('Firebase    :', error);
      }
    }
  };
  
  //    ()
  const renderUserInfo = () => {
    if (user) {
      return (
        <div style={{ 
          position: 'absolute',
          right: '200px',
          top: '0px',
          fontSize: '14px',
          color: 'green',
          display: 'flex',
          alignItems: 'center',
          gap: '10px'
        }}>
          {/*<User size={16} />
          {user.name} */}
        </div>
      );
    }
    return null;
  };

  //      
  const renderRentalDetails = (item) => {
    return (
      <div style={{ 
        display: 'flex', 
        flexDirection: 'column',
        alignItems: 'flex-start', 
        marginTop: '15px' 
      }}>
        <div style={{ marginBottom: '5px' }}>
          <span style={{ fontWeight: 'bold', marginRight: '10px' }}> :</span>
          {item.rentalDate} {item.rentalTime}
        </div>
        <div>
          <span style={{ fontWeight: 'bold', marginRight: '10px' }}> :</span>
          {item.returnDate} {item.returnTime}
        </div>
      </div>
    );
  };

  //    -  API  Firebase  
  const handleSubmitReservation = () => {
    // API_ENDPOINT    API ,  Firebase 
    if (API_ENDPOINT !== '/api/calendar') {
      createAdminCalendarEvent();
    } else {
      saveReservationToFirebase();
    }
  };

  return (
    <div style={{
      position: 'relative',
      width: '1440px',
      minHeight: '100vh',
      background: '#FFFFFF',
      margin: '0 auto',
      fontFamily: 'Pretendard, sans-serif',
      color: '#000000'
    }}>
      {/* Header Section */}
      <div style={{
        position: 'absolute',
        top: '20px',
        left: '10px',
        right: '20px',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        borderBottom: '0px solid #5F5F5F',
        paddingBottom: '45px'
      }}>
        {renderUserInfo()}
      </div>

      {/* Navigation and Logo Area */}
      <div style={{
        position: 'absolute',
        top: '20px',
        left: '20px',
        right: '20px',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        borderBottom: '0px solid #5F5F5F',
        paddingBottom: '45px'
      }}>
        <div style={{ 
          display: 'flex',
          position: 'absolute',
          gap: '20px',
          fontSize: '18px',
          fontWeight: '400',
          right: "-4px",
          top: '45px'
        }}>
          <span onClick={handleHomeNavigation} style={{ cursor: 'pointer' }}>Home</span>
          <span onClick={handleCalendarNavigation} style={{ cursor: 'pointer' }}>Calendar</span>
          <span onClick={handleReservateNavigation} style={{ cursor: 'pointer' }}>Reservation</span>
          <span onClick={handleNoteNavigation} style={{ cursor: 'pointer' }}>Note</span>
          <span onClick={handleMypageNavigation} style={{ cursor: 'pointer' }}></span>
        </div>
        <div style={{ textAlign: 'center' }}>
          <div onClick={handleHomeNavigation} style={{ 
            position: 'absolute',
            fontSize: '36px', 
            fontWeight: 'bold', 
            letterSpacing: '0px',
            top: '0px',
            left: '70px',
            cursor: "pointer"
          }}>DIRT</div>
          <div style={{ 
            fontSize: '12px', 
            color: '#000000',
            position: 'absolute',
            left: '110px',
            top: '40px',
            transform: 'translateX(-50%)',
            whiteSpace: 'nowrap',
            fontWeight: '100'
          }}>Digital content rental service</div>
        </div>
        <div style={{ display: 'flex', gap: '20px', alignItems: 'center' }}>
          <div style={{ 
            display: 'flex',
            position: 'absolute',
            right: '110px',
            top: '0px',
            alignItems: 'center', 
            gap: '5px', 
            cursor: 'pointer',
            padding: '5px 10px',
            borderRadius: '20px',
            backgroundColor: '#f0f0f0'
          }}>
            <User size={20} />
            <span onClick={handleMypageNavigation} style={{ cursor: 'pointer' }}>My page</span>
          </div>
          <div style={{ 
            position: 'absolute',
            right: '13px',
            display: 'flex', 
            top: '0px',
            alignItems: 'center', 
            gap: '5px', 
            cursor: 'pointer',
            padding: '5px 10px',
            borderRadius: '20px',
            backgroundColor: '#cccccc'
          }}>
            <ShoppingCart size={20} />
            <span>Cart</span>
          </div>
        </div>
      </div>

      {/* Cart Content Area */}
      <div style={{
        position: 'relative',
        top: '150px',
        left: '50px',
        right: '50px',
        paddingBottom: '20px'
      }}>
        <h2 style={{ 
          fontSize: '24px', 
          fontWeight: 'bold', 
          marginBottom: '20px', 
          marginLeft: '40px'
        }}>
          
        </h2>

        {loading ? (
          <div> ...</div>
        ) : cartItems.length === 0 ? (
          <div style={{ 
            textAlign: 'center', 
            color: '#888',
            padding: '50px' 
          }}>
             .
          </div>
        ) : (
          <div style={{ display: 'flex', gap: '20px' }}>
            {/* Cart Items */}
            <div style={{ 
              flex: 2, 
              border: '1px solid #E0E0E0', 
              borderRadius: '10px',
              padding: '20px'
            }}>
              {cartItems.map((item) => (
                <div 
                  key={item.id} 
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    borderBottom: '1px solid #E0E0E0',
                    paddingBottom: '15px',
                    marginBottom: '15px'
                  }}
                >
                  {/* Item Image */}
                  <div style={{ 
                    width: '150px', 
                    height: '150px', 
                    marginRight: '20px',
                    backgroundColor: '#F5F5F5',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center'
                  }}>
                    {imageUrls[item.id] ? (
                      <img 
                        src={imageUrls[item.id]} 
                        alt={item.name} 
                        style={{ 
                          width: '100%', 
                          height: '100%', 
                          objectFit: 'cover' 
                        }} 
                      />
                    ) : (
                      item.name
                    )}
                  </div>

                  {/* Item Details */}
                  <div style={{ flex: 1 }}>
                    <div style={{ 
                      display: 'flex', 
                      justifyContent: 'space-between',
                      alignItems: 'center' 
                    }}>
                      <h3 style={{ 
                        fontSize: '18px', 
                        fontWeight: 'bold' 
                      }}>
                        {item.name}
                      </h3>
                      <div 
                        style={{ 
                          cursor: 'pointer',
                          color: '#888'
                        }}
                        onClick={() => removeFromCart(item.id)}
                      >
                        <Trash2 size={20} />
                      </div>
                    </div>
                    <p style={{ color: '#666', marginTop: '5px' }}>
                      {item.category} | {item.condition}
                    </p>

                    {/* Rental Details */}
                    {renderRentalDetails(item)}
                  </div>
                </div>
              ))}
            </div>

            {/* Reservation Summary Section */}
            <div style={{ 
              flex: 1., 
              border: '1px solid #E0E0E0', 
              borderRadius: '10px',
              padding: '20px',
              marginRight :'100px',
              height: 'fit-content'
            }}>
              <h3 style={{ 
                fontSize: '20px', 
                fontWeight: 'bold', 
                marginBottom: '20px' 
              }}>
                 
              </h3>

              <div style={{ 
                display: 'flex', 
                justifyContent: 'space-between',
                marginRight: '0px',
                marginBottom: '20px',
                fontWeight: 'bold',
                fontSize: '18px'
              }}>
                <span>   </span>
                <span>{cartItems.length}</span>
              </div>

              <button 
                onClick={handleSubmitReservation}
                disabled={isSubmitting}
                style={{
                  width: '100%',
                  padding: '15px',
                  backgroundColor: isSubmitting ? '#666' : 'black',
                  color: 'white',
                  border: 'none',
                  borderRadius: '10px',
                  fontSize: '18px',
                  cursor: isSubmitting ? 'not-allowed' : 'pointer',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  gap: '10px'
                }}
              >
                <CheckCircle size={20} />
                {isSubmitting ? ' ...' : ''}
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default CartPage;